import React, { Component } from 'react'
import './About.css'
import Contact from "./Contact.js"
import sig from '../assets/white-signature.png'
import Portfolio from './Portfolio'

class About extends Component {

    state = {
        aboutButton: true,
        portfolioButton: false,
        contactButton: false
    }

    handleToggle = (e) => {
        let target = e.target.id
        if (target === 'aboutButton') {
            this.setState({
                aboutButton: true,
                portfolioButton: false,
                contactButton: false
            })
        } else if (target === 'portfolioButton') {
            this.setState({
                aboutButton: false,
                portfolioButton: true,
                contactButton: false
            })
        } else if (target === 'contactButton') {
            this.setState({
                aboutButton: false,
                portfolioButton: false,
                contactButton: true
            })
        }
        console.log(target, this.state)
    }


    render() {
        return(
            <div className="about-page">
                <div id="header">
                    <img src={sig} id="signature" alt="signature" />
                    <button className="link" id="aboutButton" onClick={this.handleToggle}>About</button>
                    <button className="link"  id="portfolioButton" onClick={this.handleToggle}>Portfolio</button>
                    <button className="link"  id="contactButton" onClick={this.handleToggle}>Contact</button>
                    <a className="link" href="https://medium.com/@morganpstanley">Blogâ¤¤</a>
                </div>
                <div className={this.state.aboutButton ? null : "hidden"} id="about">
                    <h1 className="page-header" >Welcome.</h1>
                    <p className="page-text">
                        My name is Morgan P Stanley. I'm a software developer.
                    </p>
                </div>
                <div className={this.state.contactButton ? null : "hidden"} id="contact">
                    <Contact />
                </div>
                <div className={this.state.portfolioButton ? null : "hidden"} id="portfolio">
                    <Portfolio />
                </div>
            </div>
        )
    }
}

export default About